/* ============
 * Vuex Actions
 * ============
 *
 * All the actions that can be used
 * inside the store
 */
import * as types from './mutation-types';

// Common
export const commonError = ({ commit }, err) => {
  console.error('ERROR');
  console.error(err);
};

export const showAlert = ({ commit }, message) => {
  commit(types.SHOW_ALERT, message);
};



